body {
  color: #333;
  background: #fff;
  font-family: Lato, sans-serif;
}

.container {
  width: 400px;
  margin: 100px auto 0;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
}

li * {
  float: left;
}

li, h3 {
  clear: both;
  list-style: none;
}

input, button {
  outline: none;
}

button {
  color: #888;
  width: 60px;
  cursor: pointer;
  background: none;
  border: 0;
  margin: 10px 0 0;
  font-family: Lato, sans-serif;
  font-size: 15px;
}

button:hover {
  color: #333;
}

h3, label[for="new-task"] {
  color: #333;
  text-transform: uppercase;
  border-bottom: 2px solid #333;
  margin: 0;
  padding: 30px 0 10px;
  font-size: 15px;
  font-weight: 700;
}

input[type="text"] {
  height: 18px;
  color: #888;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin: 0;
  padding: 10px;
  font-family: Lato, sans-serif;
  font-size: 18px;
  line-height: 18px;
}

input[type="text"]:focus {
  color: #333;
}

label[for="new-task"] {
  margin: 0 0 20px;
  display: block;
}

input#new-task {
  float: left;
  width: 318px;
}

p > button:hover {
  color: #0fc57c;
}

li {
  border-bottom: 1px solid #eee;
  padding: 20px 0;
  overflow: hidden;
}

li > input[type="checkbox"] {
  margin: 0 10px;
  position: relative;
  top: 15px;
}

li > label {
  width: 237px;
  padding: 0 0 0 11px;
  font-size: 18px;
  line-height: 40px;
}

li > input[type="text"] {
  width: 226px;
}

li > .delete:hover {
  color: #cf2323;
}

input[type="checkbox"]:checked + label {
  color: #888;
  text-decoration: line-through;
}

